body .contentAddres {
  display: flex;
}
body .contentAddres iframe {
  width: 60%;
  height: 600px;
}
body .contentAddres__contactForm {
  width: 40%;
  display: flex;
  flex-wrap: wrap;
  background-color: #176975;
}
body .contentAddres__contactForm .comentary {
  margin: 45px 0 0 45px;
}
body .contentAddres__contactForm .comentary form {
  width: 100%;
  position: relative;
}
body .contentAddres__contactForm .comentary form input {
  padding-left: 15px;
  font: 1.125em/45px "PTSans-Regular";
  color: #828282;
  border: 1px solid #176975;
  border-radius: 10px;
  outline: none;
}
body .contentAddres__contactForm .comentary form input[type*="text"],
body .contentAddres__contactForm .comentary form input[type*="tel"] {
  margin: 0 5px 10px;
  width: 245px;
  height: 45px;
}
body .contentAddres__contactForm .comentary form textarea {
  margin: 0 5px 10px;
  padding-left: 15px;
  width: 515px;
  height: 100px;
  font: 1.125em/45px "PTSans-Regular";
  color: #828282;
  border: 1px solid #176975;
  border-radius: 10px;
  outline: none;
  resize: none;
}
body .contentAddres__contactForm .comentary form button {
  margin: 20px 0 0 5px;
  width: 165px;
  height: 45px;
  font: 1.125em/40px "PTSans-Regular";
  color: #fff;
  text-align: center;
  background: transparent;
  border: 1px solid #fff;
  border-radius: 10px;
  outline: none;
}
body .contentAddres__contactForm .comentary form button:hover {
  background-color: #9e79ac;
  transition: .5s;
  cursor: pointer;
}
body .contentAddres__contactForm .comentary form .error {
  border: 1px solid red;
}
body .contentAddres__contactForm .comentary form #name-error {
  padding: 10px;
  position: absolute;
  left: -5px;
  top: 60px;
  text-align: center;
  background: #ffffa1;
  border: 1px solid #464040!important;
  border-radius: 5px;
}
body .contentAddres__contactForm .comentary form #name-error:before {
  content: '';
  position: absolute;
  top: -44px;
  left: 23px;
  border: 22px solid transparent;
  border-bottom: 22px solid #464040;
}
body .contentAddres__contactForm .comentary form #name-error:after {
  content: '';
  position: absolute;
  top: -40px;
  left: 25px;
  border: 20px solid transparent;
  border-bottom: 20px solid #ffffa1;
}
body .contentAddres__contactForm .comentary form #tel-error {
  padding: 10px;
  position: absolute;
  left: 270px;
  top: 60px;
  text-align: center;
  background: #ffffa1;
  border: 1px solid #464040!important;
  border-radius: 5px;
}
body .contentAddres__contactForm .comentary form #tel-error:before {
  content: '';
  position: absolute;
  top: -44px;
  left: 23px;
  border: 22px solid transparent;
  border-bottom: 22px solid #464040;
}
body .contentAddres__contactForm .comentary form #tel-error:after {
  content: '';
  position: absolute;
  top: -40px;
  left: 25px;
  border: 20px solid transparent;
  border-bottom: 20px solid #ffffa1;
}
body .contentAddres__contactForm .contacts {
  margin: 0 0 0 45px;
  width: 70%;
  display: flex;
  justify-content: space-between;
}
body .contentAddres__contactForm .contacts p {
  font: 1.125em/1.25em "PTSans-Regular";
  color: #fff;
}
body .contentAddres__contactForm .contacts p a {
  color: #fff;
  text-decoration: none;
}
body .contentAddres__contactForm .contacts p a:hover {
  text-decoration: underline;
}
body .contentAddres__contactForm .social {
  margin: 0 0 0 45px;
  width: 70%;
  display: flex;
  justify-content: space-between;
}
body .contentAddres__contactForm .social ul {
  display: flex;
  justify-content: space-between;
}
body .contentAddres__contactForm .social ul a {
  margin: 0 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5em;
  color: #176975;
  text-decoration: none;
  border-radius: 50px;
}
body .contentAddres__contactForm .social ul a i {
  font-size: 2em;
  color: #fff;
}
body .contentAddres__contactForm .social ul a:hover {
  color: #fff;
  background-color: #9e79ac;
  transition: .5s;
}
body .contentAddres__contactForm .social p {
  color: #fff;
  text-align: right;
  font: 1em/1.125em "PTSans-Bold";
}
body .contentAddres__contactForm .social p span:nth-child(1) {
  font: 0.875em/1em "PTSans-Regular";
}
